export default "class Counter extends React.Component {\r\n  constructor() {\r\n    super()\r\n    this.state = { count: 0 }\r\n  }\r\n\r\n  componentDidMount() {\r\n    this.interval = setInterval(() => {\r\n      this.setState(state => ({ count: state.count + 1 }))\r\n    }, 1000)\r\n  }\r\n\r\n  componentWillUnmount() {\r\n    clearInterval(this.interval)\r\n  }\r\n\r\n  render() {\r\n    return (\r\n      <center>\r\n        <h3>\r\n          {this.state.count}\r\n        </h3>\r\n      </center>\r\n    )\r\n  }\r\n}"